import customEvents from '~global/variables/custom-events.json';
import cookies from 'js-cookie';
import getEnvironment, { environments } from '~global-js/helpers/environment';
import fireCustomEvent from '~global-js/helpers/custom-events';

export function isApp() {
  return window.location.search.indexOf('student_app=1') !== -1 ||
    cookies.get('student_app') === '1';
}

export function getAppBridge() {
  if (window.hurricane && window.hurricane.appBridge) {
    return window.hurricane.appBridge;
  }
  return null;
}

export function onAppReady(callback) {
  if (getAppBridge() && getAppBridge().isReady()) {
    callback(getAppBridge());
  } else if (getEnvironment() !== environments.PROD && cookies.get('student_app_test')) { // for test
    callback();
  } else {
    document.addEventListener(customEvents.appReady.name, () => {
      callback(getAppBridge());
    });
  }
}

export function pageReady() {
  fireCustomEvent(customEvents.pageReady.name);
}



